import axios from "axios";

const BASE = "https://rocky-sands-70657.herokuapp.com";
// const BASE = "https://shielded-brook-33484.herokuapp.com";

const BASE_URL = BASE + "/api/users/";

//
//https://github.com/axios/axios#request-config
const axiosInstance = axios.create({
  baseURL: BASE_URL,
});

// axios (Instance)
//https://ithelp.ithome.com.tw/articles/10230336
axiosInstance.interceptors.request.use(
  (config) => {
    // Do something before request is sent
    const token = localStorage.getItem("token");
    if (token) config.headers["Authorization"] = `Bearer ${token}`;
    return config;
  },
  (error) => {
    // Do something with request error
    console.log(error);
  }
);

//[U_03] get-user-profile 
//GET /api/users/:id
export const getUserData = async (userId) => {
  if (!userId) {
    console.log("[getUserData]id");
    return;
  }
  try {
    const res = await axiosInstance.get(BASE_URL + userId);
    console.log("[]", res.data);
    return res.data;
  } catch (error) {
    console.error("[]", error);
  }
};

//[U_04] put-user-profile profile
//PUT /api/users/:id
export const putUserProfile = async (userId, data) => {
  if (!userId) {
    console.log("[putUserProfile]id");
    return;
  }
  if (!data) {
    console.log("[putUserProfile]form-data");
    return;
  }
  try {
    // console.log("[putUserProfile]", res);
    const res = await axiosInstance.put(BASE_URL + userId, data);
    return res;
  } catch (error) {
    console.error(error);
  }
};

