//component
import AuthInput from "../components/AuthInput/AuthInput";
import Logo from "../components/Logo/Logo";
//react-bootstrap
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

//react
import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";

//context
import { useAuth } from "../contexts/AuthContext";

//plugin
import Swal from "sweetalert2";

const UserLoginPage = () => {
  const navigate = useNavigate();
  const { login, isAuthenticated, currentMember } = useAuth();

  const [loginPage, setLoginPage] = useState({
    account: "",
    password: "",
  });

  const handleInput = (keyName) => (currentValue) => {
    setLoginPage({
      ...loginPage,
      [keyName]: currentValue,
    });
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    // console
    //
    if (!(loginPage.account && loginPage.password)) return;
    //Login api
    const success = await login({
      account: loginPage["account"],
      password: loginPage["password"],
    });
    //sweetAlert
    if (success) {
      // 
      // navigate("")
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      return;
    }

    // Swal.fire({
    //   position: "top",
    //   title: "",
    //   timer: 1000,
    //   icon: "error",
    //   showConfirmButton: false,
    // });
  };

  useEffect(() => {
    console.log("[LoginPage useEffect] isAuthenticated:", isAuthenticated);
    if (isAuthenticated === true) {
      navigate(`/${currentMember.id}`);
    }
  }, [navigate, isAuthenticated, currentMember]);

  return (
    <Container>
      <Row className="justify-content-center">
        <Col md={6} lg={4}>
          <Logo title={" Alphitter"} />
          <form action="">
            <AuthInput
              value={login.account}
              autoComplete="username"
              onChange={handleInput("account")}
              placeholder=""
            />
            <AuthInput
              label=""
              type="password"
              autoComplete="current-password"
              value={login.password}
              onChange={handleInput("password")}
              placeholder=""
            />
            <div className="mb-3">
              <button
                onClick={handleLogin}
                className="btn btn-primary w-100 text-white rounded-pill"
              >
                
              </button>
            </div>
          </form>
          <div className="d-flex justify-content-end">
            <Link to="/register"> Alphitter</Link>
            <span className="text-blue"></span>
            <Link to="/admin"></Link>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default UserLoginPage;
